import {Component, OnInit} from '@angular/core';
import {EventBusService} from '../../shared-service/eventBus/event-bus.service';
import {MyMeta} from '../../models';

@Component({
    selector: 'app-index',
    templateUrl: './index.component.html',
    styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {

    constructor(
        private eventBus: EventBusService
    ) {
        const meta = new MyMeta();
        meta.title = ' - Fishelly Idx.';
        meta.keyword = 'Fishelly,,,,,Front end developer,Fullstack,JavaScript,ECMAScript,HTML(5),CSS(3)';
        meta.description = ',';
        eventBus.emit('update-meta', meta);
    }

    ngOnInit() {
    }

}
